import React from 'react';
import { Nav, Navbar } from 'react-bootstrap';
import styled from 'styled-components';

import logo from '../assets/favicon64.png'

const Styles = styled.div`
    * {
        background-color: #262626;
    }
    .navbar {
        box-shadow: -1px 5px 10px #262626;
    }
    .navbar-brand, .navbar-nav, .nav-link {
        &:hover {
            color: white;
        }
    }
    img {
        border-radius: 50%;
    }
    .noSelect {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
`; 

export const NavigationBar = () => (
    <Styles>
        <Navbar className="container-fluid" expand="lg" variant="dark">
            <Navbar.Brand href="/" className="noSelect">
                <img
                    alt=""
                    src= {logo}
                    width="32"
                    height="32"
                    className="d-inline-block align-top"
                />{' '}
                Kxffie Hub
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav"></Navbar.Toggle>
            <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="ml-auto">
                    <Nav.Item><Nav.Link href="/">Home</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/about">About</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/socials">Socials</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/contact">Contact</Nav.Link></Nav.Item>
                    <Nav.Item><Nav.Link href="/projects">Projects</Nav.Link></Nav.Item>
                </Nav>
            </Navbar.Collapse>
                <span class="navbar-text float-right d-none d-lg-block noSelect">
                    React-Bootstrap
                </span>
        </Navbar>
    </Styles>
)